import React, { ReactElement } from "react";
import { withRouter } from "react-router";
import withPageLayout from "../hocs/withPageLayout";
import { PageHeaderContext, PageFooterContext } from "../context/layout";
import AppBtn from "../components/form/AppBtn";
import { List, ListItemText, ListItem } from "@material-ui/core";

const links = [
  {
    href: "/houses",
    label: "Houses",
  },
  {
    href: "/reporting-types",
    label: "Reporting Types",
  },
  {
    href: "/roles",
    label: "Roles",
  },
  {
    href: "/attendance-entities",
    label: "Attendance Entities",
  },
];

const Profile = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);
  const pageFooterContext = React.useContext(PageFooterContext);

  const addMoreBtn: ReactElement = <AppBtn title="Edit" href="/profile/edit" />;

  React.useEffect(() => {
    LayoutContext.setTitle("Profile");
    LayoutContext.setExtra([addMoreBtn]);
    pageFooterContext.setShowBottomTabBar(true);

    return () => {
      pageFooterContext.setShowBottomTabBar(false);
    };
  }, []);

  return (
    <>
      <List>
        {links.map((link: any) => {
          return (
            <ListItem button onClick={() => props.history.push(link.href)}>
              <ListItemText primary={link.label} />
            </ListItem>
          );
        })}
      </List>
    </>
  );
};
export default withRouter(withPageLayout(Profile));
